// ** React Imports
import { Link } from 'react-router-dom'

// ** Custom Components
import Avatar from '@components/avatar'

// ** Renders Client Columns
const renderClient = row => {

  return (
    <Avatar
      initials
      className='me-1'
      color={'light-primary'}
      content={row.contactname ? row.contactname.charAt(0) : 'T'}
    />
  )
}

export const columns = [
  {
    name: 'Invoice No',
    sortable: true,
    minWidth: '108px',
    selector: row => row.uniqueno,
    cell: row => <span className='text-capitalize'>{row.uniqueno}</span>
  },
  {
    name: 'Client',
    sortable: true,
    minWidth: '370px',
    sortField: 'uniqueidentity',
    selector: row => row.id,
    cell: row => (
      <div className='d-flex justify-content-left align-items-center'>
        {renderClient(row)}
        <div className='d-flex flex-column'>
          <Link
            to={`/client/view/${row.id}`}
            className='user_name text-truncate text-body'>
            <span className='fw-bolder'>{row.contactname}</span>
          </Link>
        </div>
      </div>
    )
  },
  {
    name: 'Sub Total',
    sortable: true,
    minWidth: '108px',
    selector: row => row.subtotalamount,
    cell: row => <span className='text-capitalize'>{row.subtotalamount || 0}</span>
  },
  {
    name: 'Tax Amount',
    sortable: true,
    minWidth: '108px',
    selector: row => row.totaltaxamount,
    cell: row => <span className='text-capitalize'>{row.totaltaxamount || 0}</span>
  },
  {
    name: 'Total Amount',
    sortable: true,
    minWidth: '108px',
    selector: row => row.totalamount,
    cell: row => <span className='text-capitalize'>{row.totalamount || 0}</span>
  },

  {
    name: 'Balance',
    sortable: true,
    minWidth: '108px',
    selector: row => row.dueamount,
    cell: row => <span className='text-capitalize'>{row.dueamount || 0}</span>
  }
]
